.card-container {
    background-color: #f4ebc1;
    border-radius: 20%;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out; 
  }

  .flex-container {
    display: flex;
    flex-direction: column;
  }
  
  .card-container:hover {
    transform: scale(1.05);
  }